@font-face {
  font-family: 'Kazimir';
  src: url('../fonts/CSTMFontsKazimir.eot');
  src: url('../fonts/CSTMFontsKazimir.eot') format('embedded-opentype'),
  url('../fonts/CSTMFontsKazimir.woff2') format('woff2'),
  url('../fonts/CSTMFontsKazimir.woff') format('woff'),
  url('../fonts/CSTMFontsKazimir.ttf') format('truetype'),
  url('../fonts/CSTMFontsKazimir.svg#CSTMFontsKazimir') format('svg');
}
@font-face {
  font-family: 'Circe';
  src: url('../fonts/CRC35.eot');
  src: url('../fonts/CRC35.eot') format('embedded-opentype'),
  url('../fonts/CRC35.woff2') format('woff2'),
  url('../fonts/CRC35.woff') format('woff'),
  url('../fonts/CRC35.ttf') format('truetype'),
  url('../fonts/CRC35.svg#CRC35') format('svg');
}
@font-face {
  font-family: 'Circe-Bold';
  src: url('../fonts/Circe-Bold.eot');
  src: url('../fonts/Circe-Bold.eot') format('embedded-opentype'),
  url('../fonts/Circe-Bold.woff2') format('woff2'),
  url('../fonts/Circe-Bold.woff') format('woff'),
  url('../fonts/Circe-Bold.ttf') format('truetype'),
  url('../fonts/Circe-Bold.svg#Circe-Bold') format('svg');
}

/*
* Third Party
*/
@import "../../bower_components/bootstrap-sass/assets/stylesheets/_bootstrap.scss";
@import "../../bower_components/normalize.css/normalize.css";

/*
* Custom
*/
@import "partials/_var";
@import "partials/_sprite";
@import "partials/_mobile";
@import "partials/app";
